.heading{
    font-family: 'Roboto',sans-serif;
    font-weight: 300;
    font-size: 20px;
}
.description{
    font-family: 'Roboto',sans-serif;
    letter-spacing: 0.35px;
    font-weight: 100;
    font-size: 16px;
}

@media screen and (max-width: 799px) {
.container {
    position: relative;
    text-align: center;
    color: black;
    background: rgba(0, 0, 0, 0.4);
    transition: 0.3s;
    align-items: center;
    justify-content: center;
  }
  .noimage{
    min-height:80vh;
    max-height:100vh;
    width: 100%;
    background: linear-gradient(#c471ed 30%, #f64f59 90% );
  }
  .author{
    font-family: 'Conv_ClanPro-Bold',Sans-Serif;
    color: #d3d3d3;
    font-size: 14px;
}
a { text-decoration: none; color:#d3d3d3 }
  .centered {
    color:white;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 90%;
  }
  .image{
    min-height:80vh;
    max-height:100vh;
    width: 100%;
    -webkit-filter: brightness(30%) blur(1px);
    filter: brightness(30%) blur(1px);

  }
  text{
      margin-bottom: 2vh;
  }
}  
  @media screen and (min-width: 800px) {
    .container {
        margin:0.8rem;
        width:90%;
        border-radius: 20px;
        background-color:white;
        box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
        transition: 0.3s;
        align-items: center;
        justify-content: center;
        min-height:25vh;

        width:80%;
        margin-left: auto;
        margin-right: auto;
        display: flex;
        align-items: center;
        flex-direction: row;
      }
      .author{
        font-family: 'Conv_ClanPro-Bold',Sans-Serif;
        color: #44444d;
        font-size: 14px;
    }
    a { text-decoration: none; color:#44444d }
      .container:hover {
        box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
      }
    .centered {
        color:black;
        float:right;
        width: 70%;
        margin-left: 0.8rem;
        margin-right: 0.8rem;
      }
      .image{
        float:left;
        width: 30%;
        margin:0.8rem;
        border-radius: 20px;
        box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
      }
      text{
        margin-bottom: 2vh;
        margin-left: 30px;
    }
}